
const container = document.getElementById('root');
// XMLHttpRequest ÎèÑÍµ¨
let ajax = new XMLHttpRequest(); // Î≥ÄÏàò 
// const ajax2 = new XMLHttpRequest(); // ÏÉÅÏàò
const content = document.createElement('div');
const NEWS_URL = 'https://api.hnpwa.com/v0/news/1.json';
const CONTENT_URL = 'https://api.hnpwa.com/v0/item/@id.json';
const store ={
    currentPage: 1,
};


function getData(url){
    ajax.open('GET', url , false);
    ajax.send(); // Îç∞Ïù¥ÌÑ∞ Í∞ÄÏ†∏Ïò¥

    return  JSON.parse(ajax.response);
}


console.log(ajax.response);

function newsFeed() {
    const newsFeed = getData(NEWS_URL);// ÏùëÎãµ Í∞íÏùÑ Í∞ùÏ≤¥Î°ú Î≥ÄÌôò
    const newslist = [];

    newslist.push('<ul>');

    for(let i = (store.currentPage - 1) * 10; i< store.currentPage * 10; i++){
        // ${} Î≥ÄÏàò ÌëúÌòÑ
        newslist.push(`
            <li>
                <a href="#/show/${newsFeed[i].id}">
                    ${newsFeed[i].title} (${newsFeed[i].comments_count})
                </a>
            </li>
        `);  
}
newslist.push('</ul>');
newslist.push(`
    <div>
        <a href="#/page/${store.currentPage - 1}">Ïù¥Ï†Ñ ÌéòÏù¥ÏßÄ</a>
        <a href="#/page/${store.currentPage + 1}">Îã§Ïùå ÌéòÏù¥ÏßÄ</a>
    </div>

`);

container.innerHTML = newslist.join(''); // Î∞∞Ïó¥ÏïàÏóê ÏûàÎäî ÏöîÏÜåÎì§ÏùÑ ÌïòÎÇòÏùò Î¨∏ÏûêÏó¥Î°ú Î≥ÄÌôò.
}



const ul = document.createElement('ul');
console.log(newsFeed);

function newsDetail() {// ÏûêÎ∞îÏä§ÌÅ¨Î¶ΩÌä∏Îäî Ìï®ÏàòÎ•º Í∞íÏúºÎ°ú Ï∑®Í∏â
    const id = location.hash.substr(7); // location Ï£ºÏÜåÏôÄ Í¥ÄÎ†®Îêú Í∞ùÏ≤¥(Î∏åÎùºÏö∞Ï†ÄÍ∞Ä Í∏∞Î≥∏ÏúºÎ°ú Ï†úÍ≥µ) substr() ÏßÄÏ†ïÌïú ÏúÑÏπò Ïù¥ÌõÑÏóê Í∞í Î∞òÌôò

    const newsContent = getData(CONTENT_URL.replace('@id', id));
    

    container.innerHTML =`
        <h1>${newsContent.title}</h1>

        <div>
            <a href="#">Î™©Î°ùÏúºÎ°ú</a>
        </div>
    `;
    // title.innerHTML = newsContent.title;
    // content.appendChild(title);
    console.log(newsContent);

} 

function router() {
    const routePath = location.hash;

    if(routePath == ''){ // # Ïù¥Î©¥ ÎπàÍ∞íÏùÑ Î∞òÌôò
        newsFeed();
    } else if (routePath.indexOf('#/page/') >= 0 ) { // ÏûÖÎ†• Î¨∏ÏûêÏó¥ÏùÑ Ï∞æÏïÑÏÑú 0Ïù¥ÏÉÅÏùò ÏúÑÏπòÍ∞íÏùÑ Î¶¨ÌÑ¥, ÏóÜÏúºÎ©¥ -Î•º Î¶¨ÌÑ¥
        store.currentPage = Number(routePath.substr(7));
        newsFeed();
    } else {
        newsDetail();
    }
}
window.addEventListener('hashchange', router);

router();